html {
  /* background: rgba(2, 82, 129, 0.808); */
  height: 100vh;
  margin: 0px;
  padding: 0px;
}
body{
  height: 100%;
  width: 100%;
  margin: 0px;
}
#root{
  margin: 0px;
  height: 100%;
  width: 100%;
  background-color: #357aa1;
}

.App {
  margin: 0px;
  text-align: center;
  min-height: 100%;
}

img {
  -webkit-animation: App-logo-spin infinite 15s linear;
          animation: App-logo-spin infinite 15s linear;
  max-height: 115px;
  height: 22vw;
}

.App-title {
  font-size: 1.5em;
  margin-top: 10px;
}

.App-intro {
  font-size: large;
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media (max-width: 474px) {
  .App-header{
    font-size: .9rem;
    height: 28vw;
    min-height: 80px;
  }
  
}
@media (max-width: 370px) {
   .App-header{
    /* min-height:90px; */
    /* height:50px; */
    font-size: .8rem;
    min-width: 284px;
    /* height: 125px; */
  } 
  
}


